@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainContainer {
  position: relative;
}

#navbar {
  position: fixed;
  display: inline-flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  z-index: 1000;
}

.anchor {
  margin: 15px;
  color: white;
  font-size: 70px;
  z-index: 5000;
  text-decoration: none;
  opacity: 0.8;
}

.anchor:hover {
  opacity: 1;
  transition: all 0.4s ease-out;
}

.box {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100vw;
  height: 100vh;
  color: white;
  font-size: 40px;
  font-weight: 600;
}

.overX {
  overflow-x: hidden;
}

.row {
  display: flex;
  flex-direction: row;
  width: 300%;
}

.a {
  background-color: black;
}

.mainTitle {
  position: absolute;
  left: 5%;
  bottom: 10%;
  font-size: 100px;
  font-weight: 800;
  letter-spacing: 4px;
  z-index: 100;
}

.profile {
  position: absolute;
  top: 0;
  right: 0;
  width: 45%;
  height: 100%;
  background-image: url(/portfolio-site/static/media/profile.670ab28f.jpg);
  background-size: cover;
  background-position: 50%;
}

.b {
  background-color: blue;
}

.c {
  background-color: yellow;
}

.nx {
  background-color: aqua;
}

#HomeButton {
  position: fixed;
  top: 2.5%;
  left: 2%;
  z-index: 5000;

  color: white;
  opacity: 0.6;

  font-size: 25px;
}
#HomeButton:hover {
  opacity: 1;
  transition: all 0.4s ease-out;
}

.cursor,
.follow {
  position: absolute;
  pointer-events: none;
  width: 0.4rem;
  height: 0.4rem;
  transform: translate(-50%, -50%);
}

.cursor {
  background-color: white;
  border-radius: 100%;
}

.follow {
  padding: 1rem;
  background: rgba(255, 255, 255, 0.4);
  -webkit-filter: blur(1px);
          filter: blur(1px);
  border-radius: 100%;
  transition: all 0.3s ease-out;
}

.follow.on-focus {
  transform: translate(-50%, -50%);
  border-radius: 0%;
  padding: 0;
}

* {
  margin: 0;
  padding: 0;
  font-family: 'NanumSquare', sans-serif;
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  cursor: none;
}

.title {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) translateY(-30px);
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10%;

  cursor: none;

  font-size: 60pt;
  font-weight: 800;
  letter-spacing: 4px;
  color: rgb(255, 255, 255);
  opacity: 0;
}

#link {
  position: fixed;
  top: 75%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20%;
  max-width: 160px;

  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;

  font-size: 20pt;
  font-weight: 200;
  color: white;
  text-decoration: none;
  text-align: center;
  letter-spacing: 2px;
  opacity: 0;
}

#link:hover {
  color: rgba(2, 6, 29, 0.8);
  font-weight: 500;
  cursor: none;
}

#mainPage {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  transform: translate(-50%, -50%);
  background: black;
  color: white;

  display: flex;
  justify-content: center;
  align-items: center;

  overflow: hidden;
  cursor: pointer;
}

/* left side */
#profile {
  position: absolute;
  top: 0;
  left: -25%;
  width: 75%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;

  opacity: 0;
  overflow: hidden;
}

.background-image1 {
  position: absolute;
  top: 0;
  right: -25%;
  bottom: 0;
  transform: skewX(15deg);
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: 50%;
  background-image: url(/portfolio-site/static/media/studyBook.505c4ff6.jpg);
}

#profileTitle {
  display: flex;
  justify-content: center;
  align-items: center;

  position: absolute;
  top: 50%;
  left: 25%;
  transform: translate(-50%, -50%);
  z-index: 100;
  font-size: 48px;
  font-weight: 400;
  color: white;
  text-decoration: none;

  border: 1px solid white;
  width: 300px;
  height: 300px;
  border-radius: 50%;
}
#profileTitle:hover {
  color: rgba(0, 0, 0, 0.7);
  background-color: rgba(255, 255, 255, 0.3);
  border: none;
  transition: all 0.4s ease-out;
}

/* right side */

#myWork {
  position: absolute;
  top: 0;
  right: -25%;
  width: 75%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;

  opacity: 0;
  overflow: hidden;
}

#myWork:hover {
  opacity: 1;
}

.background-image2 {
  position: absolute;
  top: 0;
  left: -25%;
  bottom: 0;
  transform: skewX(15deg);
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: 50%;
  background-image: url(/portfolio-site/static/media/cording1.532290f0.jpg);
}

#myWorkTitle {
  display: flex;
  justify-content: center;
  align-items: center;

  position: absolute;
  top: 50%;
  left: 75%;
  transform: translate(-50%, -50%);
  z-index: 100;
  font-size: 48px;
  font-weight: 400;
  color: white;
  text-decoration: none;

  border: 1px solid white;
  width: 300px;
  height: 300px;
  border-radius: 50%;
}
#myWorkTitle:hover {
  color: rgba(0, 0, 0, 0.7);
  background-color: rgba(255, 255, 255, 0.3);
  border: none;
  transition: all 0.4s ease-out;
}

